body {
    font-family: "Poppins", sans-serif;
    font-weight: 300;
    line-height: 1.5;
    font-size: 16px;
    text-align: center;
    transition: all 0.3s ease;
}







.itemList{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 150px;
    padding: 100px;

}

.item {
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 3px 15px ;
    border-radius: 10px;


}

.item img {
    width: 100%;
    height: 100%;


}

.item h3 {
    width: 80%;
    font: 700 20px "Poppins", sans-serif;
    align-self: center;
    text-align: start;


    text-transform: uppercase;
}



.item h5 {
    font: 500 15px "Poppins", sans-serif;
    font-weight: bold;


margin: 0;


}

.item p {
    margin: 0;

}





.itemDetails div {
 display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.itemDetails{
    width: 80%;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

